<template>
  <div class="others">
    <a-row :gutter="16" class="others__container">
      <a-col :span="14" class="others__content">

        <code-panel title="滚动计数">
          <template v-slot:extra>
            <a-row :gutter="16" style="padding: 0 16px">
              <a-col :span="12">
                <a-card>
                  <a-statistic
                    title="Statistics"
                    :value="statistics"
                    :precision="2"
                    suffix="%"
                    :value-style="{ color: statistics >= 0 ? '#3f8600' : '#cf1322' }"
                    style="margin-right: 50px"
                  >
                    <template #prefix>
                      <a-icon :type="statistics >= 0 ? 'arrow-up' : 'arrow-down'" />
                    </template>
                  </a-statistic>
                </a-card>
              </a-col>
              <a-col :span="12">
                <a-slider v-model="speed" @change="() => slider$.next()" />
                <a-input ref="input" placeholder="输入" />
              </a-col>
            </a-row>
          </template>
        </code-panel>

      </a-col>
      <a-col :span="10" class="others__code">

        <code-panel title="滚动计数">
          {{ countCode }}
        </code-panel>

      </a-col>
    </a-row>
  </div>
</template>

<script>
import { fromEvent, interval, merge, Subject } from 'rxjs'
import {
  debounceTime, pluck, filter, switchMap,
  startWith, take, scan, withLatestFrom,
  map
} from 'rxjs/operators'
import CodePanel from '@/components/CodePanel'

export default {
  name: 'Others',
  components: {
    CodePanel
  },
  data: () => ({
    isActive: true,
    speed: 0,
    diffCount: 0,
    statistics: 0,
    slider$: new Subject(),
    countCode: `
    //  <a-slider v-model="speed" @change="() => slider$.next()" />
    //  <a-input ref="input" placeholder="输入" />

    this.slider$: new Subject()

    const input$ = fromEvent(this.$refs.input.$el, 'keyup')
      .pipe(
        debounceTime(500),
        pluck('target', 'value'),
        filter(v => !!v)
      )

    merge(
      input$,
      this.slider$.asObservable().pipe(
        withLatestFrom(input$),
        map(([, v]) => +v)
      )
    )
      .pipe(
        switchMap(v => {
          const timer$ = interval(this.speed).pipe(startWith(0))
          this.diffCount = v - this.statistics
          return timer$.pipe(take(Math.abs(this.diffCount)), scan((acc) => {
            this.diffCount > 0 ? acc += 1 : acc -= 1
            return acc
          }, this.statistics))
        })
      )
      .subscribe(next => {
        this.statistics = next
      })
    `
  }),
  mounted () {
    const input$ = fromEvent(this.$refs.input.$el, 'keyup')
      .pipe(
        debounceTime(500),
        pluck('target', 'value'),
        filter(v => !!v)
      )

    merge(
      input$,
      this.slider$.asObservable().pipe(
        withLatestFrom(input$),
        map(([, v]) => +v)
      )
    )
      .pipe(
        switchMap(v => {
          const timer$ = interval(this.speed).pipe(startWith(0))
          this.diffCount = v - this.statistics
          return timer$.pipe(take(Math.abs(this.diffCount)), scan((acc) => {
            this.diffCount > 0 ? acc += 1 : acc -= 1
            return acc
          }, this.statistics))
        })
      )
      .subscribe(next => {
        this.statistics = next
      })
  },
  beforeDestroy () {
    this.isActive = false
  }
}
</script>

<style scoped lang="less">
.others {
  width: 100vw;
  height: calc(100vh - 64px);
  overflow: hidden;
  padding: 16px;

  &__container, &__content, &__code {
    height: calc(100vh - 64px);
  }

  &__code {
    border-left: 1px solid #000;
    overflow: auto;
  }
}
</style>
